<template>
    <div class="traveller-container">
      <div class="header-wrapper">
        <div class="header">
          <div class="back_to" @click="$router.back(-1)"></div>
          
        </div>
      </div>
      <div class="main-content">
        <div class="travel-list" v-for="(item, index) in travellers">
          <div class="list-main">
            <p><span>{{item.name}}</span><span>{{item.sexy}}</span></p>
            <p>{{item.nationality}}</p>
            <p>{{item.idnumber}}</p>
          </div>
          <div class="list-foot">
            <div class="delete"></div>
            <div class="modify"></div>
            <div class="set-default" :class="{active:item.status}" @click="changeDefault(index)"></div>
          </div>
        </div>
      </div>
      <div class="footer-fixed">
        <a href="javascript:;"></a>
      </div>
    </div>
</template>

<script>
    export default {
      name: 'traveller',
      data () {
        return {
         travellers: [
           {
             name: "",
             sexy: "",
             nationality: "",
             idnumber: 123456123456123456,
             status: true
           },
           {
             name: "",
             sexy: "",
             nationality: "",
             idnumber: 123456123456123456,
             status: false
           },
           {
             name: "",
             sexy: "",
             nationality: "",
             idnumber: 123456123456123456,
             status: false
           }
         ]
        }
      },
      methods: {
        changeDefault(n) {
          this.travellers.forEach((val)=>{
            val.status = false;
          });
          this.travellers[n].status = true;
        }
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
  @import '../../common/less/mixin';
  .traveller-container{
    padding-top:64px;
    padding-bottom: 55px;
    background-color: #f8f8f8;
    .header-wrapper{
      .header{
        .back_to{
          .bg-image("back");
        }
      }
    }
    .main-content{
      .travel-list{
        background-color: #fff;
        margin-bottom: 9px;
        padding:4px 0;
        &:last-child{
          margin-bottom: 0;
        }
        &>div{
          width:93%;
          margin:0 auto;
        }
        p{
          height:40px;
          line-height:40px;
          font-size: 14px;
          margin:0;
          span{
            margin-right: 8px;
          }
        }
        .list-main{
          .border-bottom-1px-dotted(#e8e8e8);
        }
        .list-foot{
          height:40px;
          &>div {
            float: right;
            height: 40px;
            line-height: 40px;
            margin-left: 20px;
            padding-left: 20px;
            background-repeat: no-repeat;
            background-position: left center;
          }
          .delete{
            background-size: 13px 13px;
            .bg-image("delete");
          }
          .modify{
            background-size: 14px 15px;
            .bg-image("modification");
          }
          .set-default{
            background-size: 14px 14px;
            .bg-image("Button");
            &.active{
              .bg-image("button1");
            }
          }
        }
      }
    }
  }
</style>



